import { useMemo } from "react"
import { UseContentCategoriesListState } from "./useContentCategoriesList"

type UseSourceSelectorCategoriesFlagsState = {
  shouldShowLoading: boolean
  shouldShowError: boolean
  shouldShowList: boolean
}

type UseSourceSelectorCategoriesFlagsProps = {
  contentCategoriesInfo: UseContentCategoriesListState
}

function useSourceSelectorCategoriesFlags({
  contentCategoriesInfo: {
    hasError,
    isLoadingContentCategories,
    isFetchingNextPage,
    allContentCategories,
  },
}: UseSourceSelectorCategoriesFlagsProps): UseSourceSelectorCategoriesFlagsState {
  return useMemo(() => {
    const shouldShowLoading = isLoadingContentCategories || isFetchingNextPage
    const shouldShowError = hasError
    const shouldShowList =
      !isLoadingContentCategories && !hasError && allContentCategories.length > 0

    return {
      shouldShowLoading,
      shouldShowError,
      shouldShowList,
    }
  }, [hasError, isLoadingContentCategories, isFetchingNextPage, allContentCategories])
}

export { useSourceSelectorCategoriesFlags }
export type { UseSourceSelectorCategoriesFlagsState, UseSourceSelectorCategoriesFlagsProps }
